@font-face {
	font-family: Montserrat;
	font-style: normal;
	font-weight: 300;
	src: url(https://fonts.gstatic.com/s/montserrat/v15/JTURjIg1_i6t8kCHKm45_cJD3gnD-w.ttf)
		format('truetype');
}
@font-face {
	font-family: Montserrat;
	font-style: normal;
	font-weight: 400;
	src: url(https://fonts.gstatic.com/s/montserrat/v15/JTUSjIg1_i6t8kCHKm459Wlhzg.ttf)
		format('truetype');
}
@font-face {
	font-family: Montserrat;
	font-style: normal;
	font-weight: 700;
	src: url(https://fonts.gstatic.com/s/montserrat/v15/JTURjIg1_i6t8kCHKm45_dJE3gnD-w.ttf)
		format('truetype');
}
:root {
	--color0: #f4f4f2;
	--color1: white;
	--color2: #e8e8e8;
	--color3: #bbbfca;
	--color4: #121231;
	--green: #02b290;
}
#title {
	grid-area: 1/1 / auto/3;
}
div.container {
	display: grid;
	width: 50em;
	height: 36em;
	margin: 0 auto;
	border-right: solid 1px var(--color3);
	border-left: solid 1px var(--color3);
	grid-template-columns: 12em 36em;
	grid-template-rows: 1fr 12fr;
	grid-column-gap: 2em;
}
container h1 {
	text-align: left;
	padding-left: 1em;
	height: 3em;
	line-height: 3em;
	margin-bottom: 0;
	margin-top: 0;
}

container video {
	width: 100%;
}
body {
	margin: 0;
	background-color: var(--color0);
}
header {
	background: var(--color4);
	height: 7vh;
	line-height: 7vh;
	width: 100%;
	display: flex;
}
header * {
	color: var(--color1);
}
header .navbar {
	margin: 0 auto;
	width: 50em;
	float: center;
	padding: 0 2em;
	position: relative;
}
header .navbar > h3 {
	margin: auto;
	float: left;
	display: flex;
	height: 100%;
	width: 100px;
	text-align: center;
}
header .navbar input {
	height: 3em;
	height: 2.5em;
	border-width: 0;
	padding-left: 12px;
	position: absolute;
	right: 3em;
	top: 50%;
	transform: translate(0, -50%);
	border-radius: 12px;
}
body,
body * {
	font-family: Montserrat, sans-serif;
	-webkit-font-smoothing: antialiased;
	text-rendering: optimizeLegibility;
}
@media only screen and (max-width: 600px) {
	header,
	header .navbar,
	row,
	table,
	tbody,
	td,
	th,
	tr {
		width: 100vw;
	}
	header input {
		margin: 0;
		width: 50vw;
	}
	container {
		display: block;
	}
	table {
		margin: 0;
		margin-left: 20vw;
	}
	.map-container {
		display: none;
	}
}

body {
	margin: 0;
	font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
		'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}
